<template>

    <div class="container__details">

        <div class="lds-facebook" v-if="countrySpecs.length <= 0"><div></div><div></div><div></div></div>

        <div v-else>

        <div>

        <router-link to="/"><button class="container__details--back"><i class='bx bx-arrow-back'> </i>  Volver</button></router-link>

        </div>



        <div class="container__details--countries" v-for="(country, index) in countrySpecs" :key="index">

            <figure class="container__details--image">
                <img :src="country.flag" alt="Foto de la bandera">
            </figure>

            <div class="country__details--specs">

                <h1 class="country__details--title">{{country.name}}</h1>

                <ul class="country__details--list">

                    <div class="container__details--infoOne">

                        <li class="country__details--item" v-for="(countryLenguaje, index) in countrySpecs"
                            :key="index"><span class="country__details--span">Lengua Nativa:
                            </span>{{countryLenguaje.nativeName}}</li>
                        <li class="country__details--item"><span class="country__details--span">Poblacion:
                            </span>{{country.population}}</li>
                        <li class="country__details--item"><span class="country__details--span">Region:
                            </span>{{country.subregion}}</li>
                        <li class="country__details--item"><span class="country__details--span">Capital:
                            </span>{{country.capital}}</li>

                    </div>

                    <div class="container__details--infoTwo">

                        <li class="country__details--item"><span class="country__details--span">Top Level Domain:
                            </span>{{country.topLevelDomain[0]}}</li>
                        <li class="country__details--item"><span class="country__details--span">Moneda:
                            </span>({{country.currencies[0].code}}) - {{country.currencies[0].name}}</li>

                    </div>
                </ul>


                <div class="country__details--borders" v-if="country.borders">

                <p><span class="country__details--span">Paises Limitrofes:</span></p>

                <div class="country__details--bordersOptions">

                <p class="country__details--bordersButton" v-for="(borders, index) in country.borders" :key="index">{{borders}}</p>
                </div>

                </div>

            </div>

        </div>

        </div>


    </div>

</template>

<script>
    import axios from 'axios'

    export default {

        name: 'countryDetails',

        data() {
            return {
                endpointApi: 'https://restcountries.com/v2',
                countryData: '',
            }
        },

        computed: {
            countrySpecs() {
                return this.countryData;
            }
        },

        methods: {

            async getCountryDetails() {

                try {
                    let response = await axios.get(`${this.endpointApi}/name/${this.$route.params.name}`);

                    this.countryData = response.data;

                    console.log(this.countryData);

                } catch (err) {
                    console.error(err);
                }
            }

        },

        mounted() {
            this.getCountryDetails()
        }

    }
</script>

<style lang="scss">

 $Dark-Blue: hsl(209, 23%, 22%);
    $Very-Dark-Blue-1 : hsl(207, 26%, 17%);
    $Very-Dark-Blue : hsl(200, 15%, 8%);
    $Dark-Gray : hsl(0, 0%, 52%);
    $Very-Light-Gray: hsl(0, 0%, 98%);
    $White: hsl(0, 0%, 100%);

    img {
        max-width: 100%;
        border-radius: 10px;
        object-fit: cover;
    }


    .container__details {

        height: 90vh;
        display: flex;
        justify-content: center;
        padding: 10px 20px;
       

        line-height: 2;

        &--image {
            width: 100%;
            margin-bottom: 20px;
            overflow: hidden;
        }


        &--countries {
            display: flex;
            flex-direction: column;
        }

        &--infoOne {
            margin: 20px 0;
        }

        &--back {
            background-color: #fff;
            padding: 10px 30px;
            border: none;
            border-radius: 7px;
            margin-bottom: 30px;
            box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        }

    }

    .country__details {
        &--item {
            list-style: none;
        }

        &--title {
            margin: 15px 0;
            font-size: 1.5rem;
        }

        &--span {
            font-weight: bold;
        }

         &--borders{
                display: flex;
                flex-direction: column;
                margin: 20px 0;
            }

            &--bordersOptions{
                display: flex;
                                flex-wrap: wrap;

            }

            &--bordersButton{
                margin-top: 20px;
                margin-right: 5px;
                background-color: $White;
                padding: 0px 25px;
                box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
                border-radius: 3px;
            }
    }

/* Para el modo oscuro */
    body.dark-mode{
        .container__details{
            &--back {
                color: $White;
                background-color: $Dark-Blue;
            }

        }

        .country__details{
        

            &--bordersButton{
                background-color: $Dark-Blue;
                
            }
        }
    }

    @media screen and(min-width: 720px) {

        .container__details {
             display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

            &--image {
                width: 40%;
                overflow: hidden;
            }

            &--countries {
                justify-content: space-evenly;
                flex-direction: row;
            }


        }

        .country__details{
            
            &--specs{
                width: 40%;
            }

            &--list{
                display: flex;
                align-items: baseline;
                justify-content: space-between;
            }

            &--borders{
                flex-direction: row;
            }

    


        }

    }
</style>