<template>
  <div>

    <nav class="container__nav">

      <div class="container__nav--title">
          <p class="container__nav--paragraph--principal">Where in the world?</p>
      </div>

      <div class="container__nav--mode">

          <p class="container__nav--paragraph" v-if="!darkMode"  @click="themeMode()">
            <span class="icons">
              <i class='bx bx-moon'></i>
            </span>
              Dark Mode
          </p>

          <p class="container__nav--paragraph" v-else @click="themeMode()">
            <span class="icons">
              <i class='bx bx-sun'></i>
            </span>
              Light Mode
          </p>

      </div>

  </nav>


    <router-link to="/"></router-link>
    <router-view></router-view>

  </div>
</template>

<script>


  export default {

      data() {
        return {
            darkMode: false
        }
    },


    methods: {

        themeMode(){

            this.darkMode = !this.darkMode

            if(this.darkMode){

              document.body.classList.add('dark-mode')

              localStorage.setItem('dark-mode', JSON.stringify(this.darkMode))

            }else{
                document.body.classList.remove('dark-mode')
                
              localStorage.setItem('dark-mode', JSON.stringify(this.darkMode))
            }


        }

    },

    mounted(){
      
      this.darkMode = localStorage.getItem('dark-mode')

      if(this.darkMode){
         document.body.classList.add('dark-mode')
      }else{
        document.body.classList.remove('dark-mode')
      }

    }

  }

</script>

<style lang="scss">

    @import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap');

    $Dark-Blue: hsl(209, 23%, 22%);
