    $Dark-Gray : hsl(0, 0%, 52%);
    $Very-Light-Gray: hsl(0, 0%, 98%);
    $White: hsl(0, 0%, 100%);

    *,
    ::after,
    ::before{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }

    body{
      font-family: 'Nunito Sans', sans-serif;
      background-color: $Very-Light-Gray;
        font-size: 16px;
        transition: background-color 1.5s linear;

    }

     .container__nav{
        
        display: flex;
        align-items: center;
        justify-content: space-between;
        box-shadow: 3px 2px  10px rgba(0, 0, 0, 0.288);
        padding: 20px 30px;
        position: fixed;
        width: 100%;
        background-color: #fff;
        z-index: 1000;

        &--paragraph--principal{
          font-weight: bold;
        }

    }

    .icons{
        display: inline-block;
        margin-right: 3px;
      &:hover{
        cursor: pointer;
      }      
    }

  

    /* Para el modo oscuro */
    body.dark-mode{

      background-color: $Very-Dark-Blue-1;

      .container__nav{
        background-color: $Very-Dark-Blue-1;
        color: $White;
      }

    }


</style>
