<template>
    <div class="container">

        <div class="container__main--search">

            <div class="container__main--input">

                <label for="search" class="container__main--label"><i class='bx bx-search-alt-2'></i></label>

                <input id="search" type="search" v-model="searchCountry" placeholder="Search for a country..."
                    class="input__search">

            </div>

            <div class="container__main--sort">

                <select class="container__main--region" v-model="filterCountries">
                    <option selected>{{filterCountries}}</option>
                    <option value="Africa" class="container__main--options">Africa</option>
                    <option value="America" class="container__main--options">America</option>
                    <option value="Asia" class="container__main--options">Asia</option>
                    <option value="Europe" class="container__main--options">Europa</option>
                    <option value="Oceania" class="container__main--options">Oceania</option>
                </select>

            </div>

        </div>

        <main class="container__main">

            <div class="lds-facebook" v-if="countriesPrueba.length <= 0">
                <div></div>
                <div></div>
                <div></div>
            </div>

            <article class="container__main--article" v-for="(country, index) in countriesPrueba" :key="index">

                <figure class="container__main--img">
                    <img :src="country.flag" :alt="country.name" class="img">
                </figure>

                <div class="container__main--description">

                    <router-link class="container__main--title"
                        :to="{name: 'CountryDetails', params: {name: country.name}}">

                        <h1 class="container__main--title">{{country.name}}</h1>

                    </router-link>


                    <p class="container__main--paragraph"><span class="container__main--bold">Poblacion:
                        </span>{{country.population}}</p>

                    <p class="container__main--paragraph"><span class="container__main--bold">Region:
                        </span>{{country.region}}</p>

                    <p class="container__main--paragraph"><span class="container__main--bold">Capital:
                        </span>{{country.capital}}</p>

                </div>

            </article>

        </main>

        <!--  Spinner para ir cargando mas elementos -->
        <div class="app__container" v-if="isLoading">
            <div class="spinner"></div>
            <a href="" class="click__more app__more" @click.prevent="moreData"> Ver Mas</a>
        </div>

        <router-view></router-view>

    </div>

</template>

<style lang="scss">
    $Dark-Blue: hsl(209, 23%, 22%);
    $Very-Dark-Blue-1 : hsl(207, 26%, 17%);
    $Very-Dark-Blue : hsl(200, 15%, 8%);
    $Dark-Gray : hsl(0, 0%, 52%);
    $Very-Light-Gray: hsl(0, 0%, 98%);
    $White: hsl(0, 0%, 100%);


    input {
        width: 90%;
        font-size: 16px;
        outline: none;
        border: none;
    }

    .lds-facebook {
        display: inline-block;

        position: relative;
        text-align: center;
        width: 80px;
        height: 80px;
    }

    .lds-facebook div {
        display: inline-block;
        position: absolute;
        left: 8px;
        width: 16px;
        background: #000;
        animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
    }

    .lds-facebook div:nth-child(1) {
        left: 8px;
        animation-delay: -0.24s;
    }

    .lds-facebook div:nth-child(2) {
        left: 32px;
        animation-delay: -0.10s;
    }

    .lds-facebook div:nth-child(3) {
        left: 56px;
        animation-delay: 1s;
    }

    @keyframes lds-facebook {
        0% {
            top: 8px;
            height: 64px;
        }

        100% {
            top: 24px;
            height: 32px;
        }
    }

    img {
        width: 100%;
        min-height: 250px;
        max-height: 100%;
        object-fit: cover;
    }

    .container {

        position: relative;
        top: 100px;

        &__main {

            width: 100%;
            margin-top: 50px;
            padding: 0 20px;
            display: flex;
            justify-content: space-evenly;
            flex-wrap: wrap;
            gap: 40px;

            &--search {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                padding: 0 20px;
            }

            &--label {
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 20px;
                padding: 0 20px;
                background-color: $White;
                height: 100%;
            }

            &--input {
                width: 30%;
                height: 50px;
                display: flex;
                overflow: hidden;
                border-radius: 5px;
                box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
            }

            &--sort {
                width: 20%;
            }

            &--img {
                height: 230px;
            }

            &--title {
                text-decoration: none;
                color: $Very-Dark-Blue;
            }

            &--article {
                width: 22%;
                background: $Very-Light-Gray;
                box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
                border-radius: 10px;
            }

            &--description {
                padding: 30px 20px;
                line-height: 2;
            }

            &--region {
                appearance: none;
                width: 100%;
                height: 100%;
                padding: 0 30px;
                font-size: 1rem;
                border-radius: 10px;
                box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
                outline: none;
                border: none;
            }


            &--options {
                margin-top: 20px;
            }

        }


    }

    .input__search {
        padding: 0 20px;
    }

    @media screen and (max-width: 1024px) {
        .container__main {
            &--article {
                width: 29%;
            }
        }
    }

    @media screen and (max-width: 49.5rem) {
        .container__details {

            position: relative;
            top: 100px;
        }

        .container__main {

            &--search {
                flex-direction: column;
            }

            &--article {
                width: 40%;
            }

            &--input {
                width: 100%;
            }

            &--sort {
                width: 100%;
            }

            &--region {
                margin-top: 20px;
                min-height: 50px;
            }

        }

    }

    @media screen and (max-width: 35.75rem) {
        .container__main {
            &--article {
                width: 90%;
            }
        }
    }

    //Para el modo oscuro

    body.dark-mode {

        color: $White;


        .input__search {
            transition: background-color 1.5s linear;

            background-color: $Dark-Blue;
            color: $White;

            &::placeholder {
                color: $White;
            }
        }


        .container__main {

            &--label {
                transition: background-color 1.5s linear;

                background-color: $Dark-Blue;
            }

            &--article {
                transition: background-color 1.5s linear;
                background-color: $Dark-Blue;
            }

            &--region {
                transition: background-color 1.5s linear;
                color: $White;
                background-color: $Dark-Blue;
            }

            &--title {
                text-decoration: none;
                color: $White;
            }

        }



        .lds-facebook div {

            background: $White;
        }


    }
</style>

<script>
    export default {
        name: 'country',

        data() {
            return {
                endpointApi: 'https://restcountries.com/v2',
                country: '',
                searchCountry: "",
                filterCountries: 'Filtrar por region',
                page: 15,
                hasMore: false,
                isLoading: false,
                verMas: []
            }

        },

        created() {
            this.getContries()

        },

        computed: {

            countriesPrueba() {

                if (this.searchCountry) {
                    let results = this.country.filter(country => country.name.toLowerCase().includes(this.searchCountry
                        .toLowerCase()))
                    return results
                }

                if (this.filterCountries !== 'Filtrar por region') {
                    let results = this.country.filter(country => country.region.toLowerCase().includes(this
                        .filterCountries
                        .toLowerCase()))
                    return results
                }

                return this.country
            }

        },


        methods: {

            //Peticion para traer todos los paises con sus datos
            async getContries() {

                try {

                    const response = await fetch(`${this.endpointApi}/all`)

                    const countries = await response.json();

                    this.country = countries

                    console.log(this.country);


                } catch (err) {
                    console.error(err);
                }

            },

            moreData() {
                this.page += 17;
            },

            scrolling({
                target
            }) {

                const {
                    scrollingElement
                } = target;

                const gap = 60;

                if (scrollingElement.scrollTop + scrollingElement.clientHeight + gap >= scrollingElement.scrollHeight) {
                    const button = document.querySelector('.click__button');
                    button.click()
                }

            }


        },

        mounted() {
            window.addEventListener('scroll', this.scrolling)
        },

        unmounted() {
            window.addEventListener('scroll', this.scrolling)
        }

    }
</script>